.banner-container{width:100%;max-width:100%;overflow:hidden;margin:40px 0}@media(max-width: 768px){.banner-container{margin:20px 0}}.banner-link{display:block;text-decoration:none;width:100%}.banner-image{width:100%;height:auto;-o-object-fit:cover;object-fit:cover;border-radius:0;transition:transform .3s ease}.banner-image:hover{transform:scale(1.02)}@media(max-width: 768px){.banner-image{-o-object-position:center;object-position:center;height:auto}}