x-start}.category-banner-button:hover{transform:translateY(-3px);box-shadow:0 5px 15px rgba(0,0,0,.1)}@media(max-width: 992px){.category-banner-button{padding:10px 25px;font-size:14px}}@media(max-width: 576px){.category-banner-button{padding:8px 20px;font-size:13px}}