.banner-privilege-container{width:100%;background-color:#f5f5f5;padding:30px 0;margin:40px 0;border-radius:10px}.banner-privilege-wrapper{max-width:1280px;margin:0 auto;display:flex;gap:20px;padding:0 20px}@media(max-width: 991px){.banner-privilege-wrapper{flex-direction:column}}.privileges-section{flex:3}@media(max-width: 991px){.privileges-section{width:100%}}.main-title{font-size:24px;font-weight:700;margin-bottom:20px;color:#333}@media(max-width: 768px){.main-title{font-size:20px}}.highlight{color:#2f5acf}.privilege-cards{display:flex;gap:16px}@media(max-width: 768px){.privilege-cards{flex-direction:column}}.privilege-card{background-color:#2f5acf;border-radius:8px;padding:16px;color:#fff;display:flex;justify-content:space-between;align-items:center;min-height:80px;flex:1}@media(max-width: 768px){.privilege-card{width:100%}}.card-content{display:flex;flex-direction:column}.card-title{font-size:16px;font-weight:600;margin-bottom:4px}.card-subtitle{font-size:14px;opacity:.9}.card-icon{background-color:hsla(0,0%,100%,.2);width:40px;height:40px;border-radius:50%;display:flex;align-items:center;justify-content:center}.card-icon-x2{font-weight:700;font-size:18px}.x2-text{font-weight:700}.activity-section{flex:2;border-left:1px solid #e0e0e0;padding-left:20px}@media(max-width: 991px){.activity-section{border-left:none;border-top:1px solid #e0e0e0;padding-left:0;padding-top:20px;margin-top:10px}}.activity-title{font-size:24px;font-weight:700;margin-bottom:20px;color:#333}@media(max-width: 768px){.activity-title{font-size:20px}}.activity-feed{margin-bottom:20px}.activity-item{font-size:14px;color:#666;margin-bottom:10px;line-height:1.5}.activity-user{color:#333;font-weight:600}.coolcash-amount{color:#2f5acf;font-weight:600}.join-button{display:inline-flex;align-items:center;background-color:#000;color:#fff;font-weight:600;padding:12px 24px;border-radius:30px;text-decoration:none;transition:all .2s ease}.join-button:hover{background-color:#333;transform:translateY(-2px)}.arrow-icon{margin-left:8px;font-size:18px}